/* 
  /* ________-color-______ */

:root {
  --primary: #c0b283;
  --secondary: #6e603296;
  --white: #fff;
  --black: #000000;
  --light_blue: #4d81e7;
  --text-blue: #54a6ff;
  --light_grey: #898989;
  --grey: #333333;
  --background_grey: #f1f2f4;
}
.text_light {
  color: var(--secondary);
}
.text_white {
  color: var(--white) !important;
}
.text_light_grey {
  color: var(--light_grey);
}
.text_grey {
  color: var(--grey);
}
.text_dark {
  color: var(--black);
}
.text_primary {
  color: var(--primary);
}
.text_blue {
  color: var(--text-blue);
}
.bg_primary {
  background-color: var(--primary);
}
.bg_white {
  background-color: var(--white);
}

.container_onboard {
  position: absolute;
  height: 100%;
  width: 95%;
  margin-left: 2.5%;
}

.flex_space {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  width: 100%;
}

.base_btn {
  background: var(--primary) !important;
  height: 51px;
  width: 200px;
  margin-top: 25px;
  border: none !important;
}

.onboard_modal {
  background-color: var(--primary) !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.onboard_modal_sginup {
  background-color: var(--primary) !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  width: 90%;
  margin-left: 5%;
}

.modal_btn {
  background-color: transparent !important;
  border: none !important;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 !important;
}

.input_felid {
  background: var(--secondary) !important;
  width: 100%;
  box-shadow: 0px 4px 4px 0px #00000040 inset, 0px 4px 4px 0px #00000040;
  color: var(--white);
  border: none !important;
  height: 46px;
  outline: none;
  padding: 2px 10px;
  font-family: 'raleway_regular';
}

.input_felid:focus {
  border: none !important;
}

.input_felid::placeholder {
  padding-left: 10px;
}


.modal_dark{
    box-shadow: 0px 4px 4px 0px #00000040 inset,0px 4px 4px 0px #00000040;
    background: #1E222B !important; 
    color: white !important;
    border: none !important;
    height: 46px;
    width: 170px;
    margin-top: 15px;
}
.abc div .modal-content{
    border-radius: 10px !important;
}

.main_container{
    width: 90% !important;
    margin-left: 5% !important;
}



.main_div{
    background-color: var(--secondary) !important;
    height: 100%;
    width: 100%;
    padding-bottom: 30px !important;
}

.hero_section{
 
    background-image: url('../images/home.png');
    height: 60vh;
    width: 100%;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.listing{
    background-image: url('../images/listing.png');
    width: 100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 60vh;

}

.listing_card{
    background-color: #A79A70;
    box-shadow: 0px 4px 10.300000190734863px 0px #0000002B;
    width: 80%;
}

.flex_center{
    display: flex;
    justify-content: center;
    align-items: center;
}
.showimg{
  width: 100% !important;
}

.founder_section{
  background-image: url('../images/founder.png');
  width: 100%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: 60vh;
  margin-top: 3rem;
}

.founder_des{
  background: #A79A70;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 4px 10.300000190734863px 0px #0000002B;
  padding-left: 30px;

}

.trust_section{
  background-image: url('../images/trust.png');
  width: 100%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  height: 60vh;
  margin-top: 3rem;
}

.footer{
  background-color: #665D42;
  width: 100%;
  
}



